body {
  background: #f5f5f5;
  font-family: "Arial"
}



.App {
  width: 80%;
  margin: auto;
}

.App-header {
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(170, 182, 194);

}

.App-title {
  margin: 0;
  font-size: 1em;
  text-transform: uppercase;
}

.App-title:hover {
  cursor: pointer;
}

.App-intro {
  font-size: large;
  text-align: center;
  color: white;
}

.App-results--container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



