.navButton {
    background: white;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 25px;
    color: rgb(170, 182, 194);
    -webkit-box-shadow: 0px 0px 5px #ddd;
            box-shadow: 0px 0px 5px #ddd;
}

.navButton:hover {
    cursor: pointer;
}


